import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faWhatsapp, faGithub, faHackerrank } from '@fortawesome/free-brands-svg-icons';
import { Link } from 'react-router-dom';


const SocialLinks = ({ margin }) => {

    const socialData = [
        {
            id: 1,
            icon: faLinkedin,
            url: 'https://www.linkedin.com/in/el-sayed-kewan-48565a26b/'
        },
        {
            id: 2,
            icon: faHackerrank,
            url: 'https://www.hackerrank.com/profile/elsayedkewan123'
        },
        {
            id: 3,
            icon: faGithub,
            url: 'https://github.com/Elsayed23'
        },
        {
            id: 4,
            icon: faWhatsapp,
            url: 'https://wa.me/+201152989114'
        },
    ]


    const social = socialData.map(data => {
        return (
            <Link to={data.url} key={data.id} className='text-3xl text-white hover:text-[#cccccc] duration-500' target='_blank'>
                <FontAwesomeIcon icon={data.icon} />
            </Link>
        )
    })

    return (
        <div className={`flex items-center justify-center gap-6 ${margin}`}>
            {social}
        </div>
    );
}

export default SocialLinks;