import React from 'react';
import {
    Navbar,
    Collapse,
    Typography,
    IconButton,
} from "@material-tailwind/react";
import { Bars3Icon, XMarkIcon } from "@heroicons/react/24/outline";
import { Link, useLocation } from 'react-router-dom';


function NavList({ pathname }) {


    const NavLinks = [
        {
            id: 1,
            url: '/home',
            link: 'home'
        },
        {
            id: 2,
            url: '/about',
            link: 'about'
        },
        {
            id: 3,
            url: '/projects',
            link: 'projects'
        },
        {
            id: 4,
            url: '/contact',
            link: 'contact'
        },
    ]


    const Links = NavLinks.map(link => {
        return (
            <Typography
                key={link.id}
                as="li"
                variant="small"
                className="p-1 font-medium"
            >
                <Link to={link.url} className={`flex items-center relative lg:before:absolute lg:before:h-[1px] lg:before:w-0 lg:before:left-0 lg:before:bottom-0 lg:before:bg-blue-gray-400 hover:lg:before:w-full before:duration-300 hover:text-blue-gray-400 duration-300 capitalize ${pathname === link.url && 'lg:before:w-full text-blue-gray-400'}`}>
                    {link.link}
                </Link>
            </Typography>
        )
    })

    return (
        <ul className="my-2 flex flex-col gap-2 lg:mb-0 lg:mt-0 lg:flex-row lg:items-center lg:gap-6">
            {Links}
        </ul>
    );
}


const NavBar = () => {

    const { pathname } = useLocation();

    const [openNav, setOpenNav] = React.useState(false);


    const handleWindowResize = () =>
        window.innerWidth >= 960 && setOpenNav(false);

    React.useEffect(() => {
        window.addEventListener("resize", handleWindowResize);

        return () => {
            window.removeEventListener("resize", handleWindowResize);
        };
    }, []);

    return (
        <Navbar className={`backdrop-blur-0 z-50 max-w-full shadow-none px-6 py-4 fixed transition duration-500 bg-[#0f0d0de8] bg-opacity-1 border-0 backdrop-filter-none backdrop-saturate-0 rounded-none`}>
            <div className="flex items-center justify-between">

                <Link to='/home' className='text-2xl py-1.5 font-sans font-semibold'>
                    <img src={require('../../Images/logo.png')} className='w-10 rounded-md' alt="" />
                </Link>
                <div className="hidden lg:block">
                    <NavList pathname={pathname} />
                </div>
                <IconButton
                    variant="text"
                    className="ml-auto h-6 w-6 text-inherit hover:bg-transparent focus:bg-transparent active:bg-transparent lg:hidden"
                    ripple={false}
                    onClick={() => setOpenNav(!openNav)}
                >
                    {
                        openNav
                            ?
                            (
                                <XMarkIcon className="h-6 w-6" strokeWidth={2} />
                            )
                            :
                            (
                                <Bars3Icon className="h-6 w-6" strokeWidth={2} />
                            )
                    }
                </IconButton>
            </div>
            <Collapse open={openNav}>
                <NavList pathname={pathname} />
            </Collapse>
        </Navbar>
    );
}

export default NavBar;