import React from 'react';
import ProjectCard from '../reuseableComponents/ProjectCard';
import SecTitle from '../reuseableComponents/SecTitle';
import SEO from '../reuseableComponents/SEO';
import AllProjectsData from '../../AllProjectsData'

const AllProjects = () => {

    const Projects = AllProjectsData.map(project => {
        return <ProjectCard key={project.id} {...project} />
    })

    return (
        <>
            <SEO title='My Projects' desc="This page contains the projects that I've done" />
            <div className="py-36">
                <SecTitle title='My Projects' />
                <div className="container px-4 mx-auto grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-x-6 gap-y-12 md:gap-y-10 mb-10">
                    {Projects}
                </div>
            </div>
        </>
    );
}

export default AllProjects;